@font-face {
  font-family: "Terminator Genisys";
  src: url("../../font/Terminator_Genisys.ttf") format("truetype");
  /* Дополнительные стили (если нужны) */
}

.image-container {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  transition: background-image 0.5s ease; /* Add this line for smooth transitions */
  height: calc(32vw + 285px);
  position: relative;
}

.contentBtnForWindowOne {
  display: flex;
  gap: calc(0.5vw + 10px);
  button {
    padding: calc(0.64vw + 11.7px) calc(0.64vw + 8.7px);
    font-size: calc(0.4vw + 13px);
    font-weight: 700;
    border: none;
    cursor: pointer;
    transition: background-color 0.5s, box-shadow 0.5s;
  }
  .searchPC {
    background-color: var(--bgSearchImg);
    &:hover {
      box-shadow: 5px 5px 0px 0px #5d0a3a;
    }
    &:active {
      background-color: var(--bgActiveImg);
      box-shadow: 5px 5px 0px 0px var(--bgActiveImg);
    }
  }
  .config {
    background-color: var(--bg);
    border: 2px solid var(--bgSearchImg);
    &:hover {
      box-shadow: 5px 5px 0px 0px #5d0a3a;
    }
    &:active {
      background-color: var(--bgActiveImg);
      box-shadow: 5px 5px 0px 0px var(--bgActiveImg);
    }
  }
}

.content {
  display: flex;
  flex-direction: column;
  gap: calc(0.77vw + 13px);
  width: calc(33vw + 211px);
  position: absolute;
  top: calc(8.3vw + 120px);
  left: calc(23vw + 97px);
  transform: translate(-50%, -50%);
  color: white; /* Adjust text color as needed */
  
}

.navigation {
  position: absolute;
  bottom: calc(8.5vw + 11px);
  right: 0;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  gap: calc(0.64vw + 9.7px);
  .arrow {
    background-color: var(--bgSearchImg);
    width: calc(3vw + 13px);
    height: calc(3vw + 13px);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: background-color 0.5s, box-shadow 0.5s;
    &:hover {
      box-shadow: 5px 5px 0px 0px #5d0a3a;
    }
    &:active {
      background-color: var(--bgActiveImg);
      box-shadow: 5px 5px 0px 0px var(--bgActiveImg);
    }
    img {
      width: calc(0.77vw + 11px);
    }
  }
}
.navigationBar {
  position: absolute;
  bottom: calc(8.5vw + 11px);
  left: calc(5vw + 6px);
  .navigationBarBody {
    display: flex;
    gap: calc(0.64vw + 4px);
    margin-bottom: 2px;
    .locationBar {
      width: calc(3.5vw + 13.5px);
      height: 1px;
      border: 2px solid var(--textSpan);
      background-color: var(--textSpan);
    }
  }
}

@media (min-width: 300px) {
  .paddingTop {
    padding-top: calc(3.33vw + 107px);
  }
}
@media (min-width: 601px) {
  .paddingTop {
    padding-top: calc(3.8vw + 211px);
  }
}
@media (min-width: 1401px) {
  .paddingTop {
    padding-top: calc(3.1vw + 143px);
  }
}
