.windowTen {
  display: flex;
  flex-direction: column;
  padding: 0 calc(6.4vw - 3px);
  padding-bottom: calc(5vw + 54px);
  gap: calc(1.9vw + 13px);
  background-color: var(--bg);

  .windowTenBody {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: calc(0.77vw + 9px);
    .serviceBox {
      min-width: 32%;
      max-width: 49%;
      flex-grow: 1;
      @media (max-width: 1400px) {
        min-width: 48%;
        max-width: 100%;
        flex-grow: 1;
      }
      @media (max-width: 800px) {
        min-width: 100%;
        max-width: 100%;
        flex-grow: 1;
      }
    }
  }
}
