* {
  margin: 0;
  padding: 0;
  color: var(--text);
  font-family: "Manrope", sans-serif;
}
textarea:active,
textarea:hover,
textarea:focus {
  outline: 0;
  outline-offset: 0;
}
input:active,
input:hover,
input:focus {
  outline: 0;
  outline-offset: 0;
}
.none {
  display: none;
}
.flex {
  display: flex;
}
.titleH {
  font-family: "Terminator Genisys", sans-serif;
  font-size: calc(1.9vw + 27px);
  font-weight: 400;
  line-height: 100%;
}
.titleP {
  font-size: calc(0.64vw + 11.7px);
  font-weight: 400;
  line-height: 120%;
  color: var(--textSpan);
}
.paddingTop{
  padding-top: calc(-4vw + 74px);
}


